// You can delete this file if you're not using it

const gat = require("gatsby")

const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
  }

async function fixBlogTicker(x) {
    if (!document.getElementById('Nutshell')) {
        return
    }
    if (x.matches) {
        var picture = document.getElementsByClassName("CPic")[0]
        while (picture.clientHeight === 0) {
            await sleep(100)
            picture = document.getElementsByClassName("CPic")[0]
        }
        document.getElementById("BlogTicker").style.height = String(1 * document.getElementsByClassName("CPic")[0].clientHeight) + 'px'
    } else {
        var CarouselHeight = document.getElementById('carouselExampleIndicators').offsetHeight;  
        var NutshellHeight = document.getElementById('Nutshell').offsetHeight;
        while (CarouselHeight === 0 ) {
            await sleep(100)
            CarouselHeight = document.getElementById('carouselExampleIndicators').offsetHeight;  
        }
        document.getElementById("BlogTicker").style.height = String((CarouselHeight - NutshellHeight) - 10) + 'px';
    }
}

function fixPictures() {
    var pics = document.getElementsByClassName("CPic");
    var pic;
    for (pic of pics) {
        if (pic !== pics[0]) {
            pic.style.height = String(pics[0].clientWidth) + 'px'
        }
    }
}

exports.onInitialClientRender = () => {
    var x = window.matchMedia("(max-width: 576px)");
    fixBlogTicker(x);
    x.addListener(fixBlogTicker);
    fixPictures()
    // gat.navigate("/Blog")
    // gat.navigate("/")
  }

exports.onRouteUpdate = () => {
    if (document.getElementById("BlogTicker")) {
        var x = window.matchMedia("(max-width: 576px)");
        fixBlogTicker(x);
        x.addListener(fixBlogTicker)
        fixPictures()
    }
}